.rpwe-block ul {
  list-style: none !important;
  margin-left: 0 !important;
  padding-left: 0 !important; }


.rpwe-block li {
  border-bottom: 0px solid #eee;
  margin-bottom: 10px;
  padding-bottom: 10px;
  list-style-type: none; }


.rpwe-block a {
  display: inline !important;
  text-decoration: none;
  color: $neon-green; }


.rpwe-block h3 {
  background: none !important;
  clear: none;
  margin-bottom: 0 !important;
  margin-top: 0 !important;
  font-weight: 400;
  font-size: 22px !important;
  line-height: 1.5em; }




.rpwe-thumb {
  border: 1px solid #eee !important;
  box-shadow: none !important;
  margin: 2px 10px 2px 0;
  padding: 3px !important; }


.rpwe-summary {
  font-size: 12px;
  color: $light-grey; }


.rpwe-time {
  color: #bbb;
  font-size: 11px; }


.rpwe-comment {
  color: #bbb;
  font-size: 11px;
  padding-left: 5px; }


.rpwe-alignleft {
  display: inline;
  float: left; }


.rpwe-alignright {
  display: inline;
  float: right; }


.rpwe-aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto; }


.rpwe-clearfix:before,
.rpwe-clearfix:after {
  content: "";
  display: table !important; }


.rpwe-clearfix:after {
  clear: both; }


.rpwe-clearfix {
  zoom: 1; }


.page-contact .wpcf7-not-valid-tip {
  color: $gold; }

.page-contact .wpcf7-response-output {
  border-color: $gold;
  color: $gold;
  border-radius: 5px; }

.page-contact .wpcf7-form-control.wpcf7-submit {
  margin-top: 10px;
  font-size: 16px;
  color: $accent-color;
  background-color: $accent-color-2; }

.page-contact .wpcf7-form-control.wpcf7-submit:focus,
.page-contact .wpcf7-form-control.wpcf7-submit:hover,
.page-contact .wpcf7-form-control.wpcf7-submit:active {
  background-color: darken($accent-color-2, 10); }
