.hex {
  width: 50%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  text-align: center;
  display: inline-block;
  align-self: center;
  display: flex;
  justify-content: center;
  flex-direction: column; }


.hex-bit1 {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  overflow: hidden;
  background-image: inherit;
  -ms-transform: rotate(60deg);
  -moz-transform: rotate(60deg);
  -webkit-transform: rotate(60deg);
  -o-transform: rotate(60deg);
  transform: rotate(60deg); }



.hex-bit2 {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -2;
  overflow: hidden;
  background-image: inherit;
  -ms-transform: rotate(-60deg);
  -moz-transform: rotate(-60deg);
  -webkit-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
  transform: rotate(-60deg); }


.flex-vc-text {
	display: flex;
	justify-content: center;
	flex-direction: column; }

.hex p {
	width: 100%;
	font-size: 1.2em;
	text-align: center;
	font-weight: 700;
	display: flex;
	justify-content: center;
	flex-direction: column;
	margin-bottom: 0;
	color: $light-grey; }

.hex h3 {
	color: $light-grey;
	font-size: 28px; }

@media only screen and (max-width:800px) {
	.hex p {
		font-size: 1em; } }

.hex img {
  margin-left: -50%;
  width: 200%;
  max-width: 200%; }
