.post-image-wrap {
  position: relative;
  display: flex;
  justify-content: center; }

.text-cover {
  position: absolute;
  width: 100%;
  height: 20%;
  top: 40%;
  color: white;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 54px; }

.single-work-post h1.entry-title {
  text-align: center;
  color: white;
  margin: 0; }

@media(max-width:500px) {
  .single-work-post h1.entry-title {
    font-size: 0.8em; } }

@media(max-width:400px) {
  .single-work-post h1.entry-title {
    font-size: 0.6em; } }

.post-thumbnail {
  width: 100%; }

.post-thumbnail img {
  margin: 0 auto;
  display: block;
  z-index: -1; }

.cat-links {
  font-size: 12px;
  margin-right: 10px; }

.cat-links a {
  color: $accent-color; }

time {
  font-weight: 400;
  padding-left: 4px;
  color: $accent-color;
  font-size: 16px; }


.fa-clock {
  font-size: 15px;
  color: $accent-color; }

.single-work-post .container {
  padding-top: 20px;
  max-width: 800px; }



#post-nav .col {}



.post-nav-grid {
  width: 100%;
  grid-gap: 10px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "previous next";

  align-items: center; }


.post-next {
  margin-left: 5px; }
.post-previous {
  margin-right: 5px; }

@supports (grid-gap: 10px) {
  .post-previous, .post-next {
    margin-left: 0px;
    margin-right: 0px; } }


.post-previous {
  grid-area: previous;
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  -ms-grid-row-span: 1;
  -ms-grid-row-align: center; }

.post-next {
  grid-area: next;
  -ms-grid-row: 1;
  -ms-grid-column: 2;
  -ms-grid-row-span: 1;
  -ms-grid-row-align: center; }


@media(max-width:600px) {
  .post-nav-grid {
    width: 100%;
    grid-gap: 10px;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas: "previous" "next";

    align-items: center; }

  .post-previous {
    grid-area: previous;
    -ms-grid-row: 1;
    -ms-grid-column: 1;
    -ms-grid-row-span: 1;
    -ms-grid-row-align: center; }

  .post-next {
    grid-area: next;
    -ms-grid-row: 2;
    -ms-grid-column: 1;
    -ms-grid-row-span: 1;
    -ms-grid-row-align: center; } }


.thumb-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: inherit; }

.post-previous .thumb-wrap {
  float: left; }

.post-next .thumb-wrap {
  float: right; }

.post-previous img, .post-next img {
  width: 100%;
  height: auto; }



.post-previous, .post-next {

  background-color: $bg-grey;
  height: auto;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 10px;

  small {
    color: #aaa; }
  a {

    padding-top: 10px; }
  a.thumbnail {
    border-bottom: none; }

  .box-wrap {
    margin: 15px;
    height: 150px; }

  .box-1 {
    float: right;
    padding-left: 20px; }
  .box-2 {
    float: left;
    padding-right: 20px; }

  .box-1, .box-2 {
    height: 100%;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    a {
      padding: 0; }

    h4 {
      display: flex;
      align-content: center;
      text-align: center;
      font-size: 28px; }

    small {
      position: absolute;
      bottom: 15px;
      left: 0px;
      right: 0px;
      text-align: center; } }
  .box-1 small {
    left: 20px; }
  .box-2 small {
    right: 20px; } }
