$text-color: #585757;
$charcoal: #76707d;
$pencil: #eee;


$orange: #fba519;
$gold: #FFB81C;
$red: #E03C31;
$green: #15d45e;
$purple: #8900fa;
$purpler: #6a11ad;
$light-grey: #f8f6f6;
$aquamarine: #0bde98;
$neon-green: #0bde20;

$accent-color: $purple;
$accent-color-2: $green;
$accent-color-2-dark: shade($accent-color-2, 10%);
$accent-color-2-light: tint($accent-color-2, 10%);

// wp - Copy Text and links
$color__link: $accent-color;
$color__link-hover: shade($accent-color, 10%);
$color__text-main: #232525;


// background - colors
$bg-grey: #f1f1f1;
$bg-grey-2: #333;
$bg-cream: #FBF7EF;
$bg-antiquewhite: rgba(250, 235, 215, 1);
$bg-purple: desaturate($purple, 90%);


$selection-color: #DD453C;

// nav - colors
$nav-link-white: rgba(255, 255, 255, 0.9);
$nav-link-white-hover: rgba(255, 255, 255, 1);
$nav-ul-white-hover: rgba(255, 255, 255, 0.3);
$nav-link-black: #585757;
$nav-link-black-hover: rgba(1,1,1, 0.7);
$nav-ul-black-hover: rgba(1, 1, 1, 0.1);

// social colors
$color__fb: rgba(59, 89, 152, 1);
$color__tw: rgba(29, 161, 242, 1);
$color__li: rgba(0, 119, 181, 1);
$social-color: rgba(255, 255, 255, 0.1);
$social-hover: rgba(255, 255, 255, 0.2);

// Typography
$font__main: 'Raleway', 'Helvetica', sans-serif;
$font__2: 'Oxygen', 'Helvetica', sans-serif;
$font__code: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
$font__pre: "Courier 10 Pitch", Courier, monospace;
$font__line-height-body: 1.4;
$font__line-height-pre: 1.4;


// wp - colors
$color__background-body: #ffffff;
$color__background-button: #e6e6e6;
$color__background-screen: #f1f1f1;
$color__background-pre: #eee;
$color__background-ins: #fff9c0;
$color__background-hr: #f4f4f4;

// wp - inputs
$color__text-screen: #21759b;
$color__text-input: #666;
$color__text-input-focus: #111;


// wp - buttons
$color__border-button: #ccc #ccc #bbb;
$color__border-button-hover: #ccc #bbb #aaa;
$color__border-button-focus: #aaa #bbb #bbb;
$color__border-input: #ccc;
$color__border-abbr: #666;

// WP structure

$size__site-sidebar: 0%;
$size__site-main: 100%;


//hexagon variables
$rootthree: 1.7320508075688772935274463415059;
$hex-width: 50vw;
$hex-height: ($rootthree/2)*$hex-width;
